.App {
  text-align: center;
}

*{
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}
html {
  scroll-behavior: smooth;
}
body{
  background-color: black;
  background-image: url(./archivos/imagenes/fondoimpresora5.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  font-family: 'Montserrat', sans-serif;
  color: white;
  overflow-x: hidden;
  position: relative;
}
/*NAVBAR*/

  header {
    display: flex;
    align-items: center;
    justify-content: space-between;
    height: 80px;
    padding: 0 2rem;
    background-color: white;
    color: black;
  }
  ul {
    list-style: none;
  }
  
  nav a {
    font-size: 20px;
    margin: 0 1rem;
    color: black;
    text-decoration: none;
  }
  
  nav a:hover {
    color: rgb(2, 63, 247);
    text-decoration: underline;
  }
  h3 img{
    margin-left: 100px;
    width: 150px;
  }
  header .nav-btn {
    padding: 5px;
    cursor: pointer;
    background: transparent;
    border: none;
    outline: none;
    color: var(--textColor);
    visibility: hidden;
    opacity: 0;
    font-size: 1.8rem;
  }
  
  header div,
  nav {
    margin-left: 25rem;
    display: flex;
    align-items: center;
  }
  @media(max-width:1048px){
    nav {
      margin-left: 14rem;
      display: flex;
      align-items: center;
    }
  }
  @media(min-width:1049px){
    nav {
      margin-left: 20rem;
      display: flex;
      align-items: center;
    }
  }
  @media only screen and (max-width: 924px) {
    header .nav-btn {
      visibility: visible;
      opacity: 1;
    }
  
    header nav {
      position: fixed;
      top: 0;
      left: 0;
      height: 100%;
      width: 100%;
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      gap: 1.5rem;
      background-color: var(--mainColor);
      transition: 1s;
      transform: translateY(-100vh);
    }
  
    header .responsive_nav {
      transform: none;
    }
  
    nav .nav-close-btn {
      position: absolute;
      top: 2rem;
      right: 2rem;
    }
  
    nav a {
      font-size: 1.5rem;
    }
  }
  /*MODAL*/
.popup{
  position: absolute;
  right: 0;
  top: 0;
  left: 0;
  bottom: 0;
  background-color: rgba(10, 10, 10, 0.99);
  z-index: 120;
}
.contpopup{
  margin: 0 auto;
  display: block;
  margin-top: 250px;
  width: 300px;
  height: 110px;
  background-color: lightgray;
  border-radius: 5px;
  z-index: 100;
}
.terminos{
  font-size: 15px;
  font-weight: 400; 
}
.terminos a{
  text-decoration: none;
  color: white;
 
}
.terminos a:hover{
  color: black;
  text-decoration: underline;
}
.checkbox{
 margin-left: -25px;

}
.terms{
  margin: 20px;
  margin-left: -30px;
  align-items: center;
display: grid;
grid-template-columns: repeat(2, 100px 200px);
}
.termsbutton{
  width: 100px;
  height: 30px;
}

/*PRODUCTOS*/
.section__title{
  font-family: 'Lexend Mega', sans-serif;
  font-weight: 600;
  font-size: 30px;
 line-height: 3rem;
}
.subt{
  font-size: 16px;
  font-weight: 300;
  margin-top: 1rem;
}
.containerr{
  padding: .4rem;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.category{
  /*background-image: url(./archivos/imagenes/mujer.png);*/
  background-color: rgba(0, 0, 0, 0.422);
  position: relative;
  background-repeat: no-repeat;
  background-size: cover;
  
}

.category__container{
  
  z-index: -1;
  padding-top: 4rem;
  grid-template-columns: 228px;
  justify-content: center;
  row-gap: 6rem;
  display: flex;
}
.category__card{
  position: relative;
 
  border-radius: 2rem;
  padding: 7.5rem 2.25rem 2rem;
  
  text-align: center;
  transition: background .3s;
}
.category__img{
  width: 200px;
  position: absolute;
  inset: 0;
  margin: 0 auto;
  top: -4rem;
  transition: transform .3s;
}
.category__title{
  font-size: var(--h3-font-size);
  font-weight: var(--font-semi-bold);
  color: var(--first-color);
  margin-bottom: .5rem;
}

.category__description{
  font-size: var(--small-font-size);
  width: auto;

}
.category__card:hover, .category__img{
  transform: translateY(-.5rem);
}
@media(min-width:1248px){
  .category__card{
 width: 400px;
  }
}
